@import url(https://fonts.googleapis.com/css2?family=DM+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
}

* {
  margin: 0;
  color: #000000;
  font-family: "DM Sans",sans-serif;
}




.mainLoaderOne {
    background-color: #000000;
    position: absolute;
    z-index: 20;
    width: 80%;
    height: 50vh;
    display: grid;
    align-items: flex-end;
    padding: 0% 10%;
    top: 0;
    left: 0;
}

.mainLoaderOne p {
    margin-bottom: 0.5%;
}

.mainLoaderTwo {
    background-color: #000000;
    position: absolute;
    z-index: 20;
    padding: 0% 10%;
    width: 80%;
    height: 50vh;
    bottom: 0;
    left: 0;
}
  

.mainLoaderTwo p {
    margin-top: 0.5%;
}

.mainLoaderOne p, .mainLoaderTwo p {
    font-size: 3rem;
    font-weight: 500;
    color: #ffffff;
}
nav {
    width: 100%;
    display: grid;
    height: 5.5vh;
    margin-top: 1vh;
    grid-template-columns: 50% 50%;
    align-items: center;
}

.sidenavNone {
    height: 100%;
    width: 0%;
    position: fixed;
    top: 0;
    right: 0;
    background-color: white;
    overflow-x: hidden;
    display: grid;
    padding-top: 60px;
    opacity: 0;
}

.hamburger {
    display: grid;
    float: right;
    justify-content: flex-end;
    padding-right: 8%;
}

.logo {
    padding-left: 6%;
    font-size: 2.3rem;
    font-weight: 600;
    z-index: 2;
}

.menu {
    height: 17px;
    position: fixed;
    z-index: 2;
    width: 25px;  
    display: grid;
    transition: all .2s ease-in-out;
}

.menuActive{
    height: 20px;
    position: fixed;
    z-index: 2;
    width: 30px;
    display: grid;
    transition: all .2s ease-in-out;
}


.main {
    background-color: hsla(0,0%,93.3%,.7);
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}

.menu .line {
    height: 3px;
    width: 100%;
    background-color: #000000;
    transition: all .2s ease-in-out;
    border-radius: 100px;
}


.menuActive .line:nth-child(1) {
    -webkit-transform: rotate(45deg) translateY(-7px);
            transform: rotate(45deg) translateY(-7px);
    -webkit-transform-origin: left top;
            transform-origin: left top;
    height: 3px;
    width: 100%;
    background-color: #000000;
    border-radius: 100px;
    transition: all .2s ease-in-out;
}

.menuActive .line:nth-child(2) {
    -webkit-transform: rotate(-45deg) translateY(7px);
            transform: rotate(-45deg) translateY(7px);
    -webkit-transform-origin: left bottom;
            transform-origin: left bottom;
    height: 3px;
    width: 100%;
    background-color: #000000;
    border-radius: 100px;
    transition: all .2s ease-in-out;
}

.menuActive:hover .line {
    background-color: #ffffff;
}

.menu:hover .line {
    background-color: #ffffff;
}

.sidenav {
    height: 100%;
    width: 50%;
    position: fixed;
    z-index: 1;
    top: 0;
    right: 0;
    background-color: #ffffff;
    overflow-x: hidden;
    transition: 0.5s;
    padding-top: 60px;
    display: grid;
    justify-content: flex-start;
    align-items: center;
    grid-template-columns: 100%;
    display: grid;
    place-items: center;
}

.text {
    margin-bottom: 14%;
}

.sidenav a {
    text-decoration: none;
}

.sidenav .links {
    margin-bottom: 8%;
    text-decoration: none;
    font-size: 2.15rem;
    color: #000000;
    display: block;
    transition: 0.3s;
    font-weight: 600;
}
  
.sidenav p:hover {
    color: red;
}
  
.sidenav .closebtn {
    position: absolute;
    top: 0;
    right: 25px;
    font-size: 36px;
    margin-left: 50px;
}


@media screen and (max-height: 450px) {
    .sidenav {padding-top: 15px;}
    .sidenav a {font-size: 18px;}
}


.divloader {
    width: 100%;
    height: 100vh;
    display: block;
    position: absolute;
    z-index: 16;
    margin: 0;
    top: 0;
    left: 0;
    pointer-events: none;
}

.divloader ul {
    padding: 0;
    width: 100%;
    display: grid;
    flex-direction: row;
    height: 100vh;
    grid-template-columns: repeat(5, 20%);
    list-style-type: none;
    align-items: flex-end;
}

.divloader ul li {
    width: 100%;
    height: 50vh;
    background-color: #000000;
    text-align: center;
}


.divloaderDown {
    width: 100%;
    height: 100vh;
    display: block;
    position: absolute;
    z-index: 16;
    margin: 0;
    top: 0;
    left: 0;
    pointer-events: none;
}

.divloaderDown ul {
    padding: 0;
    width: 100%;
    display: grid;
    flex-direction: row;
    height: 100vh;
    grid-template-columns: repeat(5, 20%);
    list-style-type: none;
    align-items: flex-start;
}

.divloaderDown ul li {
    width: 100%;
    height: 50vh;
    background-color: #ffffff;
    text-align: center;
}


.ring {
  position: fixed;
  top: 0;
  left: 0;
  width: 22px;
  height: 22px;
  border: 2px solid rgba(31, 30, 30, 0.808);
  border-radius: 100%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  transition-duration: 1ms;
  transition-timing-function: ease-out;
  will-change: width, height, transform, border;
  z-index: 2;
  pointer-events: none;
}

  
.ring.hovered {
  width: 50px;
  height: 50px;
  border-width: 3px;
  border-color: lightgray;
  background-color: red;
}

.ring.nav {
  width: 50px;
  height: 50px;
  border-width: 3px;
  background-color: black;
}

